import React from "react";
import "./styles.css";
import { Link } from "react-router-dom";

import logo from "../../images/apceco.png";

const Navbar = () => {
  return (
    <nav className="site-nav nav-bar-landingpage">
      <div className="container">
        <div className="site-navigation">
          <Link to="/" className="logo m-0 float-left">
            <img
              src={logo}
              style={{
                width: "170px",
                height: "56px",
                transform: "translateY(-5px)",
              }}
              alt="Logo"
            />
          </Link>
          <ul className="js-clone-nav d-none d-lg-inline-block text-start site-menu float-end nav-links-style">
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/">Our Projects</Link>
            </li>
            <li className="active">
              <Link to="/products">Products</Link>
            </li>
            <li className="has-children">
              <Link to="/services">Services</Link>
              <ul className="dropdown" style={{ width: "220px" }}>
                <li>
                  <Link to="/services/poultryequiment">Poultry Equipments</Link>
                </li>
                <li>
                  <Link to="/services/acmaintenance">A/C Maintenance</Link>
                </li>
                <li>
                  <Link to="/services/constructionequipment">
                    Construction Equipments
                  </Link>
                </li>
              </ul>
            </li>
            <li>
              <Link to="/aboutus">About us</Link>
            </li>
            <li>
              <Link to="/contactus">Contact us</Link>
            </li>
          </ul>
          <a
            href="#"
            className="burger ms-auto float-end site-menu-toggle js-menu-toggle d-inline-block d-lg-none light"
            data-toggle="collapse"
            data-target="#main-navbar"
          >
            <span></span>
          </a>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
