import React, { useState } from "react";
import "./ClientsSlider.css";

import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import c1 from "../../images/c1.png";
import c2 from "../../images/c2.png";
import c3 from "../../images/c3.png";
import c4 from "../../images/c4.png";
import c5 from "../../images/c5.png";
const logos = [c1, c2, c3, c4, c5];

const LogoSlider = () => {
  const [settings] = useState({
    dots: true,
    infinite: true,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    speed: 2000,
    autoplaySpeed: 2000,
    cssEase: "linear",
  });

  return (
    <div className="clients-container">
      <h1 style={{ textAlign: "center" }}>OUR CLIENTS</h1>
      <p style={{ textAlign: "center", paddingBottom: "20px" }}>
        Our goal is to make our clients happy with our work and to do quality
        work.
      </p>
      <Slider {...settings}>
        {logos.map((logo, index) => (
          <div key={index}>
            <img className="client-slider-imgs" src={logo} alt="logo" />
          </div>
        ))}
      </Slider>
    </div>
  );
};
export default LogoSlider;
