import React from "react";
import "./styles.css";

import Card from "../../components/ServicesCard/Card";
import Navbar from "../../components/NavBar";
import SiteFooter from "../../components/Footer";

const Index = () => {
  return (
    <div>
      <Navbar />
      <Card />
      <RecordsSection />
      <SiteFooter />
    </div>
  );
};

const RecordsSection = () => {
  return (
    <div className="section bg-white mx-2 mx-md-5 sec-services-wrap">
      <div className="container">
        <div className="row mb-5">
          <div className="col-lg-7 mx-auto text-center" data-aos="fade-up">
            <span className="subheading mb-4 d-block">OUR RECORDS</span>
            <h2 className="mb-4 heading">APCECO RECORDS</h2>
          </div>
        </div>
        <div className="row g-5">
          <div className="col-lg-4" data-aos="fade-up" data-aos-delay="0">
            <div className="service text-center">
              <span className="flaticon-wheat mb-4 d-block"></span>
              <h3 className="heading">2937</h3>
              <p>Completed Projects. </p>
              <p>
                <a href="#" className="more">
                  + Read more
                </a>
              </p>
            </div>
          </div>
          <div className="col-lg-4" data-aos="fade-up" data-aos-delay="100">
            <div className="service text-center">
              <span className="flaticon-church mb-4 d-block"></span>
              <h3 className="heading">1286</h3>
              <p>Recent Projects. </p>
              <p>
                <a href="#" className="more">
                  + Read more
                </a>
              </p>
            </div>
          </div>
          <div className="col-lg-4" data-aos="fade-up" data-aos-delay="200">
            <div className="service text-center">
              <span className="flaticon-bible mb-4 d-block"></span>
              <h3 className="heading">1398</h3>
              <p>Upcoming Projects. </p>
              <p>
                <a href="#" className="more">
                  + Read more
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Index;
