import React, { useState } from "react";
import "./ProductCard.css";

import productimage1 from "../../images/product1.png";
import productimage2 from "../../images/product2.png";
import productimage3 from "../../images/product3.png";
import productimage4 from "../../images/product4.png";

const initialProducts = [
  {
    id: 1,
    title: "Product 1",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage1,
  },
  {
    id: 2,
    title: "Product 2",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage2,
  },
  {
    id: 3,
    title: "Product 3",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage3,
  },
  {
    id: 4,
    title: "Product 4",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage4,
  },
  {
    id: 5,
    title: "Product 5",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage1,
  },
  {
    id: 6,
    title: "Product 6",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage2,
  },
  {
    id: 7,
    title: "Product 7",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage1,
  },
  {
    id: 8,
    title: "Product 8",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage2,
  },
  {
    id: 9,
    title: "Product 9",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage3,
  },
  {
    id: 10,
    title: "Product 10",
    description:
      "lorem Ipsum is simply dummy text of the printing and typesetting industry.",
    imageUrl: productimage4,
  },
];

const ProductCard = () => {
  const [products, setProducts] = useState(initialProducts);
  const [visibleProducts, setVisibleProducts] = useState(6); // Display 3 rows with 3 products each

  const showMore = () => {
    setVisibleProducts((prevVisible) => prevVisible + 6); // Display the next 3 rows
  };

  return (
    <>
      <h1 className="product-container-heading">Our Products</h1>
      <div className="product-card-container">
        <div className="product-card-grid">
          {products.slice(0, visibleProducts).map((product) => (
            <div className="product-card" key={product.id}>
              <img
                src={product.imageUrl}
                alt={product.title}
                className="product-image"
              />
              <h2 className="product-title">{product.title}</h2>
              <p className="product-description">{product.description}</p>
              <button className="product-button">Add to Cart</button>
            </div>
          ))}
        </div>
        {visibleProducts < products.length && (
          <button className="show-more-button" onClick={showMore}>
            Show More
          </button>
        )}
      </div>
    </>
  );
};

export default ProductCard;
