import React from "react";

import Navbar from "../../components/NavBar";
import SiteFooter from "../../components/Footer";
import ClientSlider from "../../components/OurClients/ClientsSlider";
import GeneralManagerMessage from "../../components/GeneralManagerMessage/GeneralManagerMessage";

import t1 from "../../images/t1.jpeg";
import t2 from "../../images/t2.jpeg";
import t3 from "../../images/t3.jpeg";
import t4 from "../../images/t4.jpeg";

const Index = () => {
  return (
    <div>
      <Navbar />
      <AboutUsSection />
      <GeneralManagerMessage />
      <MemberList />
      <OurClients />
      <SiteFooter />
    </div>
  );
};

const AboutUsSection = () => {
  return (
    <div className="section">
      <div className="container">
        <div className="row justify-content-between">
          <div className="col-lg-4" data-aos="fade-up">
            <span className="subheading mb-4 d-block">About us</span>
            <h2 className="mb-4 heading">
              ARAB POULTRY AND <br /> CATTLE EQUIPMENT
            </h2>
            <p>
              The Arab Poultry and Cattle Equipment co. (APCECO) is a share
              holding company sponsored by Fujairah Government located in the
              new industrial area in Fujairah. Arab Poultry and Cattle Equipment
              Co. (APCECO) is one of the biggest factories in the Middle East,
              which is specialized in poultry and cattle equipment.
            </p>
            <a href="#" className="btn btn-primary">
              Know more about us
            </a>
          </div>
          <div className="col-lg-6" data-aos="fade-up" data-aos-delay="100">
            <img
              src="http://www.apceco.ae/assets/images/background1.jpg"
              alt="Image"
              className="img-fluid rounded"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

const OurClients = () => {
  return <ClientSlider />;
};

const MemberList = () => {
  const members = [
    {
      name: "Mr Danish",
      role: "Founder & CEO",
      imageSrc: t1,
    },
    {
      name: "Mr Madhu",
      role: "Product Manager",
      imageSrc: t2,
    },
    {
      name: "Eng Shakeeb",
      role: "Marketing Head",
      imageSrc: t3,
    },
    {
      name: "Mr Brando",
      role: "Affiliate Manager",
      imageSrc: t4,
    },
  ];

  return (
    <div className="container">
      <h1 style={{ textAlign: "center" }}>OUR FANTASTIC TEAM</h1>
      <ul className="list-members">
        {members.map((member, index) => (
          <li key={index} className="member">
            <div
              className="member-image"
              style={
                member.name === "Mr Danish"
                  ? { transform: "translateX(-2vw)", width: "61%" }
                  : {}
              }
            >
              <img src={member.imageSrc} alt={`Image of ${member.name}`} />
            </div>
            <div className="member-info">
              <h3>{member.name}</h3>
              <p>{member.role}</p>
              <div className="social-link">
                <i className="fa fa-facebook-f"></i>
                <i className="fa fa-twitter"></i>
                <i className="fa fa-linkedin"></i>
              </div>
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Index;
